<template>
  <div v-loading="loading">
    <div class="resume-warp">
      <div class="resume-head">
        <div class="img-wrap">
          <img :src="detailData.photoUrl" alt="" />
        </div>
        <div class="info">
          <h3>{{detailData.name}} <i :class="['icon',detailData.sex === 0 ? 'girl' : 'boy']"></i></h3>
          <ul class="info-list">
            <li><i class="icon sui"></i>30岁</li>
            <li><i class="icon nian"></i>7年</li>
            <li><i class="icon xueli"></i>{{detailData.qualifications}}</li>
            <li><i class="icon zaizhi"></i>{{detailData.workStatus}}</li>
          </ul>
        </div>
        <div class="btn-bar">
          <el-button type="primary" plain @click="setCvCollectFun">收藏</el-button>
           <el-button type="primary">联系TA</el-button>
        </div>
      </div>
    </div>
    
    <div class="resume-detail">
      <div class="detail-panel">
        <h3>个人优势</h3>
        <div class="content">
          {{detailData.proDesc}}
          <!-- <ul class="singlerow-list">
            <li>1、产品Roadmap&需求收集-分析能力</li>
            <li>2、基于Agile/Scrum的敏捷产品开发方法论</li>
            <li>1、产品Roadmap&需求收集-分析能力</li>
          </ul> -->
        </div>
      </div>
      <div class="detail-panel">
        <h3>工作信息</h3>
        <div class="content">
          <ul class="multiplerow-list">
            <li><label>期望职位：</label><span>{{detailData.reqPositon}}</span></li>
            <li><label>行业：</label><span>{{detailData.reqIndustry}}</span></li>
            <li><label>薪资：</label><span>{{detailData.reqSalary}}</span></li>
          </ul>
        </div>
      </div>
      <div class="detail-panel">
        <h3>教育经历</h3>
        <div class="content">
          <ul class="multiplerow-list">
            <li><i class="jiaoyu-icon xueli"></i><span>{{detailData.school}}</span></li>
            <li><i class="jiaoyu-icon zhuanye"></i><span>{{detailData.speciality}}</span></li>
            <li><i class="jiaoyu-icon boshi"></i><span>{{detailData.qualifications}}</span></li>
          </ul>
        </div>
      </div>
      <div class="detail-panel">
        <h3>工作经历</h3>
        <div class="content">
          <ul class="multiplerow-list work">
            <li><i class="jiaoyu-icon qiye"></i><span>阿里巴巴（中国）网络技术有限公司</span></li>
            <li><i class="jiaoyu-icon zhiwei"></i><span>环保工程师</span></li>
            <li><i class="jiaoyu-icon date"></i><span>2015.05~2020.06</span></li>
          </ul>
          <ul class="multiplerow-list work">
            <li><i class="jiaoyu-icon qiye"></i><span>阿里巴巴（中国）网络技术有限公司</span></li>
            <li><i class="jiaoyu-icon zhiwei"></i><span>环保工程师</span></li>
            <li><i class="jiaoyu-icon date"></i><span>2015.05~2020.06</span></li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {getCvDetail,setCvCollect} from '@/api/resume'
export default {
  name: "resumeDetail",
  data(){
    return {
      loading:false,
      param:{},
      detailData:{}
    }
  },
  mounted(){
    console.log(this.$route.params)
    this.param = this.$route.query;
    getCvDetail(this.param.id).then(res=>{
      console.log(res)
      this.detailData = res
      // if(res.code === 200) {
      //   this.detailData = res.data
      // }
    })
  },
  methods: {
    setCvCollectFun(){
      this.loading = true;
      let _param = {
        comId:'',
        cvId:this.param.id
      }
      setCvCollect(_param).then(res=>{
        if(res.code === 200){
            this.$message({
          message: '操作成功',
          type: 'success'
        });
        }else {
            this.$message.error(res.msg);
        }
        this.loading = false;
      })
    }
  },
};
</script>
<style lang="less" scoped>
@import "../../assets/variable.less";
.resume-warp {
  min-width: 1200px;
  background-image: url(../../assets/img/search_bg.png);
  background-position: top center;
  background-size: 100% 100%;
}
.resume-head {
  position: relative;
  width: 1200px;
  margin: 0 auto;
  padding: 25px 45px;
  box-sizing: border-box;
  text-align: left;
  .img-wrap,
  .info {
    .inline-block;
  }
  .img-wrap {
    width: 120px;
    height: 120px;
    border-radius: 100%;
    overflow: hidden;
  }
  .info {
    margin-left: 20px;
    & > h3 {
      font-size: 20px;
      color: #fff;
      font-weight: 400;
    }
    .icon {
      .inline-block;
      width: 28px;
      height: 28px;
      background-size: 100% 100%;
      &.boy {
        background-image: url(../../assets/img/boy.png);
      }
      &.girl {
        background-image: url(../../assets/img/girl.png);
      }
    }
  }
  .info-list {
    li {
      .inline-block;
      margin-right: 30px;
      color: #fff;
    }
    .icon {
      width: 27px;
      height: 27px;
      margin-right: 10px;
      background-position: center;
      background-repeat: no-repeat;
      &.sui {
        width: 40px;
        height: 40px;
        background-image: url(../../assets/img/sui.png);
      }
      &.nian {
        background-image: url(../../assets/img/nian.png);
      }
      &.xueli {
        width: 25px;
        height: 25px;
        background-image: url(../../assets/img/xueli.png);
      }
      &.zaizhi {
        height: 22px;
        background-image: url(../../assets/img/zaizhi.png);
      }
    }
  }
  .btn-bar {
    position: absolute;
    right: 25px;
    top: 50%;
    margin-top: -20px;
    white-space: nowrap;    
  }
}
.resume-detail {
  min-width: 1200px;
  background-image: url(../../assets/img/newContentBg.jpg);
  background-position: top center;
  // background-size: 100% 100%;
}
.detail-panel {
  width: 1200px;
  margin: 0 auto 15px;
  color: #333;
  text-align: left;
  h3 {
    padding-top:5px;
    line-height: 30px;
    border-bottom: 1px #80d3f8 solid;
  }
  .content {
    font-size: 13px;
    padding: 10px 0;
  }
}
.singlerow-list {
  line-height: 20px;
}
.multiplerow-list {
  &.work {
      border-bottom: 1px #797979 solid;
      padding: 20px 0;
      &:last-child {
        border: 0
      }
    }
  li {
    .inline-block;
    margin-right: 30px;
  }
  .jiaoyu-icon {
    .inline-block;
    width: 30px;
    height: 30px;
    margin-right: 10px;
    background-position: center;
    background-size: auto 80%;
    background-repeat: no-repeat;
    &.xueli {
      background-image: url(../../assets/img/xueli.png);
    }
    &.zhuanye {
      background-image: url(../../assets/img/zhuanye.png);
    }
    &.boshi {
      background-image: url(../../assets/img/boshi.png);
    }
    &.qiye {
      background-image: url(../../assets/img/qiye.png);
    }
    &.zhiwei {
      background-image: url(../../assets/img/zhiwei.png);
    }
    &.date {
      background-image: url(../../assets/img/date.png);
    }
  }
}
</style>


