// 简历信息管理接口
import api from '../utils/api';

// 获取简历列表
export async function getCvList(params) {
    const path = '/env/cv/getList';
    const data = await api.post(path, params);
    return data;
}
// 获取简历列表
export async function getCvDetail(params) {
    const path = '/env/cv/getCvById/'+params;
    const data = await api.get(path);
    return data;
}
// 收藏简历
export async function setCvCollect(params) {
    const path = '/env/cv/cvCollect';
    const data = await api.post(path,params);
    return data;
}



